.container {
    min-height: calc(100vh - 80px);
    display: flex;
    align-items: start;
    background-color: rgba(238, 205, 208, 0.89);
}

.paper {
    width: 600px;
    margin-left: 200px;
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}

.photo {
    width: 100%;
}

.name {
    font-size: 40px;
    letter-spacing: 4px;
    font-weight: bold;
    text-shadow: -2px -2px 1px #b148e3;
}

.status {
    margin-bottom: 20px;
}

.pets {
    width: 100%;
    margin-top: 50px;
    margin-left: 40px;
    display: flex;
    flex-direction: column;
    align-items: center;

}

.title {
    font-size: 30px;
    font-weight: bold;
    color: #2c541c;
    margin-bottom: 30px;
}

@media screen and (max-width: 800px) {
    .title {
        margin-bottom: 10px;
    }

    .paper {
        margin-left: 40px;
    }
}

@media screen and (max-width: 550px) {
    .paper {
        width: 300px;
    }

    .pets {
        align-items: start;
        margin-left: 10px;
    }
}